import { Html } from "@react-three/drei";

const BackGroundFront = (props) => {
  return(
    <Html 
      fullscreen 
      zIndexRange={[10000000000000, 0]}
      calculatePosition={ () => [0, 0, 0] }
      className="BackGroundFront"
    >
      <div className="frontText">
        {props.num == 1 ? 
          <div className="first">
            <h1>S4 Celestial</h1>
            <div>
              FR<br/>
              V8<br/>
              300km/h
            </div>
          </div> :
        props.num == 2 ? 
        <div className="second">
          <h1></h1>
          <div>
            <br/>
            
          </div>
        </div> :
        props.num == 3 ? 
        <div className="box left">
          <h1></h1>
          <div>
            S4 Celestial
          </div>
        </div> :
        props.num == 4 ? 
        <div className="box right">
          <h1></h1>
          <div>
          </div>
        </div> :
        props.num == 5 ? 
        <div className="box right">
          <h1></h1>
          <div>
            
          </div>
        </div> :
        props.num == 6 ? 
        <div className="box left">
          <h1></h1>
          <div>
            
          </div>
        </div> :
        props.num == 7 ? 
        <div className="box left">
          <h1></h1>
          <div>
            BluetoothAM/FMApple CarPlayAndroid Auto
          </div>
        </div> :
        props.num == 8 ? 
        <div className="box right">
          <h1></h1>
          <div>
            
          </div>
        </div> :
        props.num == 9 ? 
        <div className="box left">
          <h1></h1>
          <div>
            2
          </div>
        </div> :
        props.num == 10 ? 
        <div className="box right">
          <h1></h1>
          <div>
            
          </div>
        </div> :
        props.num == 11 ? 
        <div className="third">
          {!props.configulater ? 
            <>
            <span>
              <h1></h1>
              <div>
                S4 Celestial
              </div>
            </span>
            <button onClick={() => props.setConfigulater(!props.configulater)}>{props.configulater ? '' : ''}</button>
            </>
          : 
            undefined
          }
          
        </div>
        : undefined}
      </div>
    </Html>
  )
}

export default BackGroundFront;